import Link from "next/link";
import { withRouter } from "next/router";

function NavLink({ href, name, scroll, router }) {
  return (
    <Link
      href={href}
      passHref
      scroll
      className={router.pathname === href ? "red" : "black"}
    >
      {router.pathname}
    </Link>
  );
}

export default withRouter(NavLink);
